import QR from '@chenfengyuan/vue-qrcode';
import { confirmPassword } from '@nextcloud/password-confirmation';
import '@nextcloud/password-confirmation/dist/style.css';
import { showError } from '@nextcloud/dialogs';
import { getRootUrl } from '@nextcloud/router';
import NcButton from '@nextcloud/vue/dist/Components/NcButton.js';
import Check from 'vue-material-design-icons/Check.vue';
import ContentCopy from 'vue-material-design-icons/ContentCopy.vue';
export default {
  name: 'AuthTokenSetupDialogue',
  components: {
    Check,
    ContentCopy,
    NcButton,
    QR
  },
  props: {
    add: {
      type: Function,
      required: true
    }
  },
  data() {
    return {
      adding: false,
      loading: false,
      deviceName: '',
      appPassword: '',
      loginName: '',
      copied: false,
      showQR: false,
      qrUrl: ''
    };
  },
  computed: {
    copyTooltipOptions() {
      if (this.copied) {
        return t('settings', 'Copied!');
      }
      return t('settings', 'Copy');
    }
  },
  methods: {
    selectInput(e) {
      e.currentTarget.select();
    },
    submit() {
      confirmPassword().then(() => {
        this.loading = true;
        return this.add(this.deviceName);
      }).then(token => {
        this.adding = true;
        this.loginName = token.loginName;
        this.appPassword = token.token;
        const server = window.location.protocol + '//' + window.location.host + getRootUrl();
        this.qrUrl = "nc://login/user:".concat(token.loginName, "&password:").concat(token.token, "&server:").concat(server);
        this.$nextTick(() => {
          this.$refs.appPassword.select();
        });
      }).catch(err => {
        console.error('could not create a new app password', err);
        OC.Notification.showTemporary(t('settings', 'Error while creating device token'));
        this.reset();
      });
    },
    async copyPassword() {
      try {
        await navigator.clipboard.writeText(this.appPassword);
        this.copied = true;
      } catch (e) {
        this.copied = false;
        console.error(e);
        showError(t('settings', 'Could not copy app password. Please copy it manually.'));
      } finally {
        setTimeout(() => {
          this.copied = false;
        }, 4000);
      }
    },
    reset() {
      this.adding = false;
      this.loading = false;
      this.showQR = false;
      this.qrUrl = '';
      this.deviceName = '';
      this.appPassword = '';
      this.loginName = '';
    }
  }
};